@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

$primaryColor: #1f2937;
$primaryTextColor: #fff;
$hoverBgColor: #8d8d8d6b;
$sidebarLogoColor: #9ca3af;
$iconColor: #4527a4;
$iconBgColor: #4427a42f;
$borderColor: #ccc;

.bpwsnbDashboard {
  padding: 20px;
  margin: 0 0 0 -20px;
  border-radius: 3px;
  overflow: hidden;

  .saveBtnWrap {
    bottom: 0;
    right: 0;
    margin-left: auto;

    button {}
  }
}

.wp-dashboard-main {

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }

  ul,
  ol {
    list-style: none;
  }

  p,
  ul,
  li,
  span {
    font-size: 15px;
  }

  font-family: "Roboto",
  sans-serif !important;
  letter-spacing: 0.75px;
  display: flex;

  .wp-sidebar-main {
    width: 255px;
    // padding-bottom: 20px;
    background-color: $primaryColor;

    .wp-sidebar-wrapper {
      // logo section
      color: $primaryTextColor;

      .wp-sidebar-logo-container {
        padding: 20px;

        .wp-sidebar-logo {
          img {
            // height: 45px;
            object-fit: cover;
          }
        }
      }

      //menu items style
      .wp-sidebar-items-wrapper {
        margin: 10px;

        .wp-sidebar-items {
          .wp-sidebar-item-wrapper {
            cursor: pointer;
            padding: 7px 10px;
            position: relative;
            margin-bottom: 5px;

            &:hover {
              background-color: $hoverBgColor;

              .wp-sidebar-item-logo {
                color: #fff;
                fill: #fff;
              }
            }

            display: flex;
            align-items: center;
            justify-content: space-between;

            .wp-sidebar-item {
              .wp-sidebar-item-title {
                text-transform: uppercase;
                font-weight: 500;
              }

              .wp-sidebar-description {
                font-size: 13px;
              }
            }

            .wp-sidebar-item-logo {
              color: $sidebarLogoColor;
              fill: $sidebarLogoColor;
            }
          }

          .active {
            background-color: $hoverBgColor;
          }

          .wp-sidebar-children-wrapper {
            display: flex;
            align-items: center;
            gap: 8px;
            // border-top: 1px solid #838383;
            cursor: pointer;
            padding: 5px 15px;
            position: relative;
            border-radius: 10px;

            // background-color: $hoverBgColor;
            &:hover {
              background-color: $hoverBgColor;

              .wp-sidebar-item-logo {
                color: #fff;
              }
            }

            // &:hover::after {
            //   content: "";
            //   position: absolute;
            //   width: 3px;
            //   background-color: red;
            //   left: 0;
            //   top: 0;
            //   bottom: 0;
            // }
            .wp-sidebar-children-logo {
              width: 30px;
              height: 30px;
              display: flex;
              align-items: center;
            }

            .wp-sidebar-children-content {
              .wp-sidebar-children-title {}

              .wp-sidebar-description {}
            }
          }
        }
      }
    }
  }

  // sidebar style end

  //body content style start
  .wp-dashboard-bodycontent-mainWrapper {
    padding: 20px;
    background: #fff;
    flex: 1 1 auto;
    max-width: 1230px;

    .wp-dashboard-header-main {
      border-bottom: 1px solid $borderColor;
      padding-bottom: 20px;
      position: relative;

      // &::after {
      //   content: "";
      //   position: absolute;
      //   width: 42px;
      //   height: 3px;
      //   background: $iconColor;
      //   bottom: 0px;
      //   left: 0px;
      // }

      .wp-dashboard-header-wrapper {
        display: flex;
        gap: 30px;
        align-items: center;

        .wp-dashboard-header-logo-container {
          .wp-dashboard-header-logo {
            background: #000;
            height: 45px;
            width: 42px;
            display: flex;
            align-items: center;
            justify-content: center;
            // border-radius: 3px;

            svg {
              // color: $iconColor;
              // fill: $iconColor;
            }
          }
        }

        .wp-dashboard-header-title-container {
          .wp-dashboard-header-title {
            h2 {
              font-size: 24px;
              font-weight: 500;
              text-transform: capitalize;
            }
          }
        }

        .saveBtnWrap {
          button {
            background: #4527a4;
            border: none;
            font-size: 15px;
            color: #e7e7e7;
            height: 40px;
            padding: 0 20px;
            border-radius: 5px;
            transition: all 0.2s ease-in-out;
            cursor: pointer;

            &:hover {
              background-color: #5e41bb;
            }

            .saveBtn-loader {
              position: absolute;
              top: 34%;
              right: 20px;
              --d: 7px;
              width: 2px;
              height: 2px;
              border-radius: 50%;
              color: #fff;
              box-shadow: calc(1 * var(--d)) calc(0 * var(--d)) 0 0,
                calc(0.707 * var(--d)) calc(0.707 * var(--d)) 0 0.2px,
                calc(0 * var(--d)) calc(1 * var(--d)) 0 0.4px,
                calc(-0.707 * var(--d)) calc(0.707 * var(--d)) 0 0.6px,
                calc(-1 * var(--d)) calc(0 * var(--d)) 0 0.8px,
                calc(-0.707 * var(--d)) calc(-0.707 * var(--d)) 0 1px,
                calc(0 * var(--d)) calc(-1 * var(--d)) 0 1.2px;
              animation: saveBtnLoader 1.3s infinite steps(8);
            }

            @keyframes saveBtnLoader {
              100% {
                transform: rotate(1turn);
              }
            }
          }

          .paddingRight {
            padding-right: 35px;
          }

          .isDisabled {
            background-color: #5e41bb;
          }
        }
      }
    }

    .settingArea {
      .route {
        display: flex;
        width: 100%;
        gap: 10px;
        margin-top: 10px;

        .components-base-control {
          width: 100%;
        }

        .disable {
          input {
            background-color: #7c7c7c52;
          }
        }

        a {
          width: 200px;
          border: 1px solid #949494;
          display: flex;
          align-items: center;
          justify-content: center;
          height: 30px;
          font-size: 13px;
          font-weight: 400;
          text-decoration: underline;
          color: #000;
        }

        .disable {}
      }
    }
  }
}